<script>
import functions from "../../util/functions.js";
import constants from "../../util/constants";
// import DatePicker from "vue2-datepicker";

export default {
  props: {
    add_mode: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    ...functions, 
    verifierChargement() {
      this.nbr_charges += 1;
      if (this.nbr_charges == this.nbr_total_chargements) {
        this.is_loading_naissances = false;
      }
    },
    async loadArticle() {
      let url =
        this.constants.api_url +
        "/naissances/get.php?id_naissance=" +
        encodeURIComponent(this.$route.params.id_naissance);
      let response = null;
      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        
        response = await fetch(url, {
          method: "get",
          cache: "no-cache",
          headers 
        });
      } catch (err) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: err.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS" && data.agent) {
        this.agent = {
            ...data.agent,
            // est_disponible :   data.agent.est_disponible == 1 ? true : false,
            // est_en_promotion :   data.agent.est_en_promotion == 1 ? true : false,
            // est_nouveau :   data.agent.est_nouveau == 1 ? true : false, 
        };
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
    },
    async loadCollections() {
      let url =
        this.constants.api_url +
        "/collections/get.php" ;
      let response = null;
      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        
        response = await fetch(url, {
          method: "get",
          cache: "no-cache",
          headers 
        });
      } catch (err) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: err.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS" && data.collections) {
        this.listCollections = data.collections;
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
    },
    onImageAdded(image) {
      this.agent.images = [...this.agent.images, image];
    },
    onImageUpdated(image) {
      let index = this.agent.images.findIndex(
        (img) => img.id_image == image.id_image
      );
      if (index >= 0) {
        this.agent.images[index] = image;
      }
    },
    onImageDeleted(image) {
      this.agent.images = this.agent.images.filter(
        (img) => img.id_image != image.id_image
      );
    },
    verifierAgent(agent) {
      let message = "";
      if (agent.nom == undefined || agent.nom == "") {
        message = "Veuillez entrer un nom correcte svp";
      } else if (agent.postnom == undefined || agent.postnom == "") {
        message = "Veuillez entrer un postnom correcte svp";
      }  else if (agent.prenom == undefined || agent.prenom == "") {
        message = "Veuillez entrer un prénom correcte svp";
      }  else if (agent.sexe == undefined || agent.sexe == "") {
        message = "Veuillez sélectionner un sexe svp";
      } else if (agent.phone == undefined || agent.phone == "") {
        message = "Veuillez entrer un numéro de téléphone correcte svp";
      } else if (agent.email == undefined || agent.email == "") {
        message = "Veuillez entrer une adresse email correcte svp";
      }  else if (agent.password == undefined || agent.password.length < 5) {
        message = "Veuillez entrer un mot de passe ayant au moins 5 caractères";
      } else if (agent.password!== agent.conf_password) {
        message = "Les deux mots de passe ne concordent pas";
      }  else if (agent.type == undefined || agent.type == "") {
        message = "Veuillez sélectionner un type de compte";
      } 
      //  else if (agent.status == undefined || agent.status=="") {
      //   message = "Veuillez sélectionner un status correcte pour le compte";
      // }   
      
      if (message != "") {
        this.$swal({
          type: "warning",
          title: "Les données fournies sont incorrcetes",
          text: message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      return {
        status: message === "",
        message,
      };
    },
    async doAddAgent() {
      if (!this.verifierAgent(this.agent).status) return;

      this.loading_message = "Enregistrement du decès  en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/users/";
      let response = null;

      let obj = {
            ...this.agent,
            service_id : this.utilisateur.service_id
          }
      delete obj.conf_password

      try {
         let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        response = await fetch(url, {
          method: "post",
          cache: "no-cache",
          headers  : {
            "Content-Type" : "application/json"
          },
          body:   JSON.stringify(obj), 
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.success) {
         
        this.agent = this.default_naissance; 

        this.$snotify.success("L'agent a été enregistré avec succès.", {
          timeout: 1500,
          showProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          
        }); 
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
    async doEditArticle() {
      if (!this.verifierAgent(this.agent).status) return;

      this.loading_message = "Enregistrement des modifications  en cours";
      this.is_doing_action = true;
      let url =
        this.constants.api_url + "/naissances/edit.php/";
      let response = null;

      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        
        response = await fetch(url, {
          method: "post",
          cache: "no-cache",
          credentials: "include",
          headers,
          body: "id_naissance=" + encodeURIComponent(this.$route.params.id_naissance) + 
          "&nom=" +
            encodeURIComponent(this.agent.nom) +
            "&collection_id=" +
            encodeURIComponent(this.agent.collection_id) +
            "&prix_unitaire=" +
            encodeURIComponent(this.agent.prix_unitaire) +
            "&est_en_promotion=" +
            encodeURIComponent(this.agent.est_en_promotion) +
            "&prix_barre=" +
            encodeURIComponent(this.agent.prix_barre) +
            "&est_disponible=" +
            encodeURIComponent(this.agent.est_disponible) +
            "&est_nouveau=" +
            encodeURIComponent(this.agent.est_nouveau) +
            "&description=" +
            encodeURIComponent(this.agent.description),
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        let profil_msg = "";
        if (this.ifile) {
          let rr = await this.doUpdateProfilImage(
            this.$route.params.id_naissance
          );
          if (rr.status == "SUCCESS") {
            profil_msg = "La mise à jour du profile a été effectué avec succès";
          } else if (rr.status == false) {
            profil_msg =
              "La mise à jour de l'image a renvoyé un erreur : " + rr.status;
          }
        }
        this.agent = this.default_naissance;
        this.deleteImage();
        
        if(profil_msg != '')  {
          this.$snotify.info(profil_msg, {
            timeout: 1500,
            showProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            
          });
        }

        this.$snotify.success("L'agent a été modifé avec succès.", {
          timeout: 1500,
          showProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          
        });

        

        // await this.$swal({
        //   type: "success",
        //   title: "Effectué",
        //   text: "Le agent a été éditée avec succès. " + profil_msg,
        //   //footer: '<a href>Why do I have this issue?</a>'
        // });
        this.$router.push("/admin/naissances");
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
    async doUpdateProfilImage(id_naissance) {
      if (this.profile_image_changed && this.ifile) {
        this.loading_message = "Mise à jour de l'image en cours";
        let url =
          this.constants.api_url + "/naissances/change-image.php?id_naissance=" + id_naissance;

        const formData = new FormData();
        formData.append("img", this.ifile, this.ifile.name);

        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        let response = await fetch(url, {
          method: "post",
          body: formData,
          credentials: "include",
          cache: "no-cache",
          // headers,
        });

        let data = await response.json();
        if (data.status == "SUCCESS") {
          return {
            status: "SUCCESS",
          };
        } else {
          return data;
        }
      } else {
        return { status: "NO_CHANGE" };
      }
    },
    onProfilImageChange(event) {
      let file = event.target.files[0];
      this.filename = "";
      this.preview_img = null; //defaultPreviewImg;
      this.preview_img_base64 = "";
      this.ifile = null;
      if (!file) return;
      let reader = new FileReader();
      reader.onload = () => {
        this.img_profil = reader.result;
        let base64 = reader.result.substr(
          reader.result.indexOf(",") + 1,
          reader.result.length - (reader.result.indexOf(",") + 1)
        );
        this.filename = file.name;
        this.ifile = file;
        this.preview_img = this.img_profil;
        this.preview_img_base64 = base64;

        this.profile_image_changed = true;
        // setImagesChanged(true);
      };
      reader.readAsDataURL(file);
    },
    deleteImage() {
      this.filename = "";
      this.preview_img = null; //defaultPreviewImg;
      this.preview_img_base64 = "";
      this.ifile = null;
      this.profile_image_changed = true;
    },
    getImgObjModel(
      id_naissance,
      nom_fichier = "",
      id_image = -1,
      description = ""
    ) {
      return {
        description,
        id_naissance: id_naissance,
        code_contexte: "IMG_JOUEUR",
        nom_fichier,
        id_image,
      };
    },
  },
  async created() {
    try {
      this.utilisateur = JSON.parse(sessionStorage.getItem('utilisateur'));
    } catch(e) {
      this.utilisateur = {id : -1}
    }
    if (!this.add_mode) {
     this.nbr_total_chargements += 1;
      this.is_loading_naissances = true;
      this.loadArticle().then(() => this.verifierChargement()); 
    }
    
    this.loadCollections().then(() => this.verifierChargement()); 
  },
  data() {
    return {
      constants,

      nbr_total_chargements: 1,
      nbr_charges: 0,

      sortBy: " ",
      perPage: 10,
      currentPage: 1,
      sortDesc: false,
      sortByFormatted: true,
      filterByFormatted: true,
      sortable: true,
      mouvements_naissance: [],
      fields: [
        { key: "date_mouvement", sortable: true },
        { key: "etat_naissance", sortable: false },
        { key: "observation", sortable: true },
        // { key: "action", sortable: true },
      ],

      is_loading_naissances: false,
      is_doing_action: false,
      listCollections : [],
      agent: {  
        nom : "",
        postnom : "",
        prenom : "",
        sexe : "",
        adresse : "",
        email : "",
        phone : "",
        password : "",
        conf_password : "",
        type : "Agent",
        status : "1",
        service_id : -1
      },
      default_naissance: { 
        nom : "",
        postnom : "",
        prenom : "",
        sexe : "",
        adresse : "",
        email : "",
        phone : "",
        password : "",
        conf_password : "",
        type : "Agent",
        status : "1",
        service_id : -1
      },
      utilisateur: {},
      loading_message: "Chargement de données en cours",

      // sur la preview de l'image de profil
      filename: "",
      preview_img: null,
      preview_img_base64: "",
      ifile: null,
      img_profil: null,
      profile_image_changed: false,
    };
  },
  components: { 
    // DatePicker
  },
};
</script>

<template>
  <section class="news-grid mt-5">
    <div class="page-header">
      <h3 class="page-title">
        {{
          add_mode ? "Enregistrer un agent" : "Editer les informations sur une Article"
        }}
      </h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link :to="{ name: 'dashboard' }"
              >Saisie de données</router-link
            >
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            {{
              add_mode
                ? "Enregistrer un agent"
                : "Editer les informations sur un Article"
            }}
          </li>
        </ol>
      </nav>
    </div>

    <div class="mt-5 pt-4" v-if="is_loading_naissances">
      <div class="card">
        <div class="card-body">
          <div class="circle-loader"></div>
          <p class="text-center mt-4">Chargement de données en cours</p>
        </div>
      </div>
    </div>
    <div class="row mt-5" v-else-if="!is_loading_naissances">
      <div class="col-md-12">
        <div class="card mb-3" v-if="is_doing_action">
          <div class="card-body">
            <div class="circle-loader"></div>
            <p class="text-center mt-4">{{ loading_message }}</p>
          </div>
        </div>

        <div class="card mb-4">
          <div class="card-body">   
            <div class="row">
            <div class="col-md-7">
              <fieldset class="mt-4" :disabled="is_doing_action">
                <b-form-group label="Nom  :" label-for="nom">
                    <b-form-input
                    type="text"
                    class="mb-3"
                    id="nom"
                    v-model="agent.nom"
                    placeholder="Nom"
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Postnom  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="agent.postnom"
                    placeholder="Postnom "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Prénom  :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="agent.prenom"
                    placeholder="Postnom "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Sexe :" label-for="sexe_defun">
                    <select id="sexe" v-model="agent.sexe" class="form-control">
                        <option   :value="'Masculin'">Masculin</option>
                        <option   :value="'Feminin'">Féminin</option>
                    </select>
                </b-form-group>

                <b-form-group label="Email :" >
                    <b-form-input
                    type="email"
                    class="mb-3" 
                    v-model="agent.email"
                    placeholder="Email"
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Mot de passe  :" >
                    <b-form-input
                    type="password"
                    class="mb-3" 
                    v-model="agent.password"
                    placeholder="Mot de passe "
                    ></b-form-input>
                </b-form-group>

                <b-form-group label="Confirmer le mot de passe  :" >
                    <b-form-input
                    type="password"
                    class="mb-3" 
                    v-model="agent.conf_password"
                    placeholder="Confirmer le Mot de passe "
                    ></b-form-input>
                </b-form-group>

                <!-- <b-form-group label="Type de compte :" label-for="sexe">
                    <select id="sexe" v-model="agent.type" class="form-control">
                        <option   :value="'Agent'">Agent</option>
                        <option   :value="'Admin'">Admin</option>
                        <option   :value="'User'">Utilisateur</option>
                    </select>
                </b-form-group> -->

 
                <b-form-group label="Adresse :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="agent.adresse"
                    placeholder="Adresse "
                    ></b-form-input>
                </b-form-group>

                
 
                <b-form-group label="Téléphone :" >
                    <b-form-input
                    type="text"
                    class="mb-3" 
                    v-model="agent.phone"
                    placeholder="Téléphone"
                    ></b-form-input>
                </b-form-group>

                <!-- <b-form-group label="Statut du compte :" label-for="sexe">
                    <select id="sexe" v-model="agent.status" class="form-control">
                        <option   :value="'1'">Actif</option>
                        <option   :value="'0'">Désactivé</option>
                    </select>
                </b-form-group> -->

                <b-button
                    type="submit"
                    :disabled="is_doing_action"
                    variant="success"
                    class="mr-2 mt-4"
                    @click="add_mode ? doAddAgent() : doEditArticle()"
                    >{{
                    add_mode
                        ? "Enregistrer l'agent"
                        : "Enregistrer les modifications apportées"
                    }}</b-button
                >
                </fieldset>
            </div>

             
            </div>  
          </div>
        </div>
      </div>
    </div>
 
  </section>
</template>
<style scoped>
.detail-agent-item {
  font-size: 1.08em;
}
.biographie-text {
  font-size: 1.1em;
}
</style>